@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300&display=swap');

.home-container{
    display: flex;
    flex-direction: column;
}

.showcase-container{
    position: relative;
}

.showcase{
    font-family: 'Open Sans', sans-serif;
    height: 100vh;
    background-color: rgba(48, 83, 117, 0.8);
    position: absolute;
    top: 0px;
    left: 0px;
    color: white;
    width: 100%;
}

.showcase:before{
    content: '';
    background: url("../../Images/Home-Background-Image.jpg") no-repeat center center/cover;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: -1;
}



.showcase .showcaseInner{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align:center;
    height: 100%;
}

.showcase h1{
    font-size: 3.5rem;
    font-weight: 500;
    margin: 0px;
}



.description{
    width: 60%;
    font-size: 17px;
    font-weight: 550;
    color: rgb(237, 248, 253);
}

.projectsBtn{
    height: 30px;
    width: 120px;
    background-color: rgb(239, 247, 255);
    color: rgb(83, 111, 138);
    border: none;
    border-radius: 15px;
    font-size: 15px;
    font-weight: 500;
}

.projectsBtn:hover{
    cursor: pointer;
    background-color: rgb(255, 255, 255);
}

@media(max-width:780px){
    .showcase h1{
        font-size: 3rem;
    }

    .showcase p{
        font-size: 1rem;
    }
}

@media(max-width:600px){
    .showcase h1{
        font-size: 2rem;
    }

    .showcase p{
        font-size: 0.7rem;
    }
}