@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-container{
    display: flex;
    flex-direction: column;
}

.showcase-container{
    position: relative;
}

.showcase{
    font-family: 'Open Sans', sans-serif;
    height: 100vh;
    background-color: rgba(48, 83, 117, 0.8);
    position: absolute;
    top: 0px;
    left: 0px;
    color: white;
    width: 100%;
}

.showcase:before{
    content: '';
    background: url(/react-portfolio/static/media/Home-Background-Image.8784cb96.jpg) no-repeat center center/cover;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: -1;
}



.showcase .showcaseInner{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align:center;
    height: 100%;
}

.showcase h1{
    font-size: 3.5rem;
    font-weight: 500;
    margin: 0px;
}



.description{
    width: 60%;
    font-size: 17px;
    font-weight: 550;
    color: rgb(237, 248, 253);
}

.projectsBtn{
    height: 30px;
    width: 120px;
    background-color: rgb(239, 247, 255);
    color: rgb(83, 111, 138);
    border: none;
    border-radius: 15px;
    font-size: 15px;
    font-weight: 500;
}

.projectsBtn:hover{
    cursor: pointer;
    background-color: rgb(255, 255, 255);
}

@media(max-width:780px){
    .showcase h1{
        font-size: 3rem;
    }

    .showcase p{
        font-size: 1rem;
    }
}

@media(max-width:600px){
    .showcase h1{
        font-size: 2rem;
    }

    .showcase p{
        font-size: 0.7rem;
    }
}


.project-container{
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(auto-fill, 400px);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    justify-content: center;
}

.fade-in-project {
	opacity: 1;
	-webkit-animation-name: fadeInOpacityProjects;
	        animation-name: fadeInOpacityProjects;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease-in;
	        animation-timing-function: ease-in;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
}

@-webkit-keyframes fadeInOpacityProjects {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes fadeInOpacityProjects {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}


@media(max-width:800px){
    .project-container{
        grid-template-columns: repeat(auto-fill, 500px);
    }
}

@media(max-width:420px){

    .project-container{
        grid-template-columns: repeat(auto-fill, 350px);
        justify-content: center;

    }
}

.project-item{
    display: flex;
    align-items: flex-end;
    width: 400px;
    height: 270px;
}

.project-item .item-btns-container{
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: rgba(0, 0, 0, 0.411);
    height: 80px;
    width: 100%;
}

.code-btn, .site-btn{
    text-align: center;
    background-color: rgb(255, 255, 255);
    border-style: none;
    height: 30px;
    width: 90px;
    border-radius: 15px;
    font-size: 17px;
    font-weight: 550;
    color: rgb(82, 82, 82);
}

.code-btn:hover, .site-btn:hover{
    background-color: orange;
    color: white;
    cursor: pointer;
}

.project-container{
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(auto-fill, 400px);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    justify-content: center;
}


@media(max-width:800px){
    .project-item{
        width: 500px;
        height: 350px;
    }
}

@media(max-width:420px){
    .project-item{
        width: 350px;
        height: 250px;
    }

}


.about-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: 'Open Sans', sans-serif;
    position: relative;
    width: 100%;
    overflow: hidden;
}

.about-center{
    display: flex;
    flex-direction: row;
    height: 500px;
    width: 80%;
}

.about-text{
margin-top: 50px;
position: absolute;
left: 550px;
width: 1235px;
height: 300px;

font-family: Roboto;
font-style: normal;
font-weight: bold;
font-size: 270px;
line-height: 169px;
display: flex;
align-items: center;
text-align: center;

color: rgba(82, 144, 202, 0.8);

opacity: 0.7;
-webkit-transform: rotate(-7.5deg);
        transform: rotate(-7.5deg);
}

.about-info-container{
    position: relative;
    width: 500px;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: center ;
    align-items: center;
    
}

.about-info-container::after{
    content: '';
    width: 500px;
    height: 500px;  
    position: absolute;
    top: 0;
    left: 0;
    background: #FFF5F5;
    opacity: 0.71;
}

.about-info-1, .about-info-2{
    font-family: Arial, Helvetica, sans-serif;
    text-align: justify;
    font-weight: 600;
    font-size: 1.1rem;
    color: #1e3c69;
    line-height: 120%;
    z-index: 1;
}

.about-info-1{    
    width: 300px;
    height: 200px;
}

.about-info-2{    
    width: 300px;
    height: 100px;
}


.fade-in {
	opacity: 1;
	-webkit-animation-name: fadeInOpacity;
	        animation-name: fadeInOpacity;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease-in;
	        animation-timing-function: ease-in;
	-webkit-animation-duration: 0.4s;
	        animation-duration: 0.4s;
}

@-webkit-keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

